:root {
  --pri-font:"Ubuntu", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(216, 93%, 44%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.logo:hover {
  opacity: 1 !important;
}

nav a {
  transition: opacity 0.2s ease-in-out !important;
}
nav a:hover {
  opacity: 0.6;
}

a {
  transition: opacity 0.2s ease-in-out !important;
}
a:hover {
  opacity: 0.6;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 0.8s ease-in-out;
}

.form {
  width: min(95%, 28.125rem);
}
@media (max-width: 63.9375em) {
  .form {
    margin: 0 auto;
  }
}
.form input, .form textarea {
  transition: all 0.2s ease-in-out !important;
}
.form input:focus, .form textarea:focus {
  outline: 1px solid var(--blue);
}
.form input:not(:first-child), .form textarea:not(:first-child) {
  margin-top: 1.8rem !important;
}
.form button {
  transition: opacity 0.2s ease-in-out !important;
}
.form button:hover {
  opacity: 0.8;
}
.form button:disabled {
  opacity: 0.2;
  pointer-events: none;
}
.form i {
  opacity: 0.5;
}

